
img.bg {
  width: 100%;
  height: 160px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}
.shop_info {
  height: 48px;
  position: relative;
}
.shop_icon {
  width: 48px;
  height: 48px;
  position: absolute;
  left: 0; top: 0;
}
.shop_icon > img {
  width: 36px;
  height: 36px;
  position: absolute;
  left: 0; top: 6px;
}
div.shop_name {
  height: 48px;
  line-height: 48px;
  margin-left: 46px;
  color: #333333;
  font-size: 16px;
  font-weight: 300;
}
.info_box {
  width: 84%;
  margin: 30px auto 0;
  background-color: white;
  height: 180px;
  border-radius: 10px;
  padding: 10px 15px 10px 15px;
  z-index: 1000;
  position: relative;
  box-shadow: 0 0 1px #d1e0f7,
              0 0 1px #d1e0f7,
              0 0 10px #d1e0f7;
}
.amount_box {
  width: 80%;
  height: 36px;
  margin: 30px auto 0;
  position: relative;
  border-bottom: 1px solid #e5e5e5;
}
.amount_box > table {
  width: 100%;
  height: 100%;
  border: none;
  border-collapse: collapse;
}
.amount_box .title {
  width: 34px;
  font-size: 14px;
  font-weight: 300;
  color: #a0a0a0;
  position: absolute;
  left: 0;
  bottom: 0;
}
.amount {
  width: 100%;
  height: 100%;
  font-size: 28px;
  color: #333333;
  padding-left: 34px;
  text-align: center;
}
.amount > span {
  position: relative;
}
.placeholder {
  color: #cbcbcb !important;
}
.symbol {
  font-size: 14px;
  color: #a0a0a0;
  font-weight: 300;
}
.tips {
  display: inline-block;
  text-align: center;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 8px;
  font-size: 10px;
  color: #b4b4b4;
}
.copyright {
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 266px;
  left: 0;
  color: #b4b4b4;
  font-size: 12px;
}
.confirm_normal {
  background-color: #3485fb;
}
.confirm_disabled {
  background-color: #ABCDFF;
}
.keyboard {
  width: 100%;
  height: 260px;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #fff;
}
.keyboard table {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
  border: 1px solid #dedfe2;
  box-sizing: border-box!important;
}
.keyboard td {
  width: 25%;
  border: 1px solid #dedfe2;
  text-align: center;
}
.keyboard td > div {
  width: 100%;
  height: 100%;
  line-height: 65px;
  background-color: #fff;
  position: relative;
}
.keyboard div.key {
  color: #424857;
  font-size: 32px;
  font-weight: 300;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  position: absolute;
}
.keyboard div.active:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.1)
}
.keyboard div.del_key {
  background-image: url('../../../static/images/icon/c.svg');
  background-repeat: no-repeat;
  background-size: 35px auto;
  background-position: 50%;
}
.key span {
  display: block;
  line-height: 28px;
  font-size: 22px;
  color: white;
  font-weight: 400;
}
.key span.tp {
  margin-top: 70px;
}



